
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import ActionSheet from 'components/ActionSheet/actionsheet.vue';
export default {
components:{
XHeader,InlineLoading,ActionSheet
},
beforeRouteLeave(to, from, next){  // 用于做缓存
if (to.name == 'EditPatient' || to.name=='appointmentDetails' || to.name=='addMedicalCard') {
localStorage.setItem('timeList', JSON.stringify(this.$route.params.item));
localStorage.setItem('doctorInfo', JSON.stringify(this.$route.params.doctorInfo));
}else{
localStorage.removeItem('timeList')
localStorage.removeItem('doctorInfo')
this.$destroy()
}





next();
},
mounted(){
this.timeList = this.$route.params.item || JSON.parse(window.localStorage.timeList); 
this.doctorInfo = this.$route.params.doctorInfo || JSON.parse(window.localStorage.doctorInfo);  
this.getYYtime();  
this.qryPatients();  

},
activated(){    // 路由缓存 进入的钩子函数
this.qryPatients();
if(this.cache){
this.qryMedicalCard(this.cache);
}

},
data(){
return {
state:false,
JZKshow:false,
timeList:'',   // 排班信息
doctorInfo:'',   // 医生基本信息
YYSD:'', // 预约时段
JZlist:[],  // 就诊人列表
JZKlist:[],  // 就诊卡列表
Patients:'',  // 就诊人
MedicalCard:'',  // 就诊卡
PatientsInfo:'',    // 就診人信息
JZRJLBM:'',    // 就诊人记录编码
cache: '',  // 缓存的就诊人
}
},
methods: {
getCZYJSZC(str){
switch(str){
case '01':
return '主任医师'
break;
case '02':
return '副主任医师'
break;
case '03':
return '主治医师'
break;
case '04':
return '医师'
break;
case '05':
return '医士'
break;

}
},

getLost(IHYS,IYYHYS){
if(typeof IHYS!='undefined'&&typeof IYYHYS!='undefined'){
return '剩余'+(IHYS-IYYHYS);
}else{
return '';
}
},
getYYtime(){
let time =  this.timeList.DBEGIN ? this.timeList.DBEGIN.split(' ')[0] : '';
let weekArr = ['周日','周一','周二','周三','周四','周五','周六']
let week = weekArr[new Date(time).getDay()];
let str = this.timeList.ISJD ? '下午' : '上午';
this.YYSD = `${time}&ensp;${str}&ensp;(${week})`
},
qryPatients(){  // 获取就诊人列表
this.$vux.loading.show({
text: '加载中...'
})
this.$ajax.post('',{
"MSH.1": "visitor",
"MSH.2": "visiterList",
token:this.$root.loginInfo.token, //token
pageIndex:1,
pageSize:100

}).then(res => {
this.$vux.loading.hide();
this.JZlist = [];
if(res.rows){
res.rows.forEach(item=> {
this.JZlist.push({
name: item.CHZXM,
key: item,
});
});
}
}).catch(error => {
console.log(error);
})
},
addPatients(){  // 添加就诊人
this.$router.push({
name:'EditPatient'
});

},
qryMedicalCard(obj){ //    查询就诊卡
this.JZKlist = [];
this.MedicalCard = '';
this.Patients = obj.name;
this.PatientsInfo = obj.key;
this.cache = obj;
this.$vux.loading.show({
text: '加载中...'
})
this.$ajax.post('',{
"MSH.1": "visitcard",
"MSH.2": "pageVisitCard",
token:this.$root.loginInfo.token, //token
jzrBm:obj.key.CBM, // 就诊人记录编码
pageIndex:1,
pageSize:10

}).then(res => {
this.$vux.loading.hide();
if(typeof res.rows != 'undefined' && res.rows.length > 0){
this.MedicalCard = res.rows[0].CBM;
res.rows.forEach(index=> {
this.JZKlist.push({
name: `${index.CYYMC}(${index.CBM})`,  // 就诊医院+就诊卡号
key: index,
})
});
}

}).catch(error => {
console.log(error);
})
},
addMedicalCard(){   // 添加就诊卡
this.$router.push({
name:'addMedicalCard',
params:{
PatientsInfo: this.PatientsInfo,
hospital:this.doctorInfo.CMC,
hospitalBM:this.doctorInfo.YLJGID,
}
});
},
selectJZK(obj){    // 选择就诊卡
this.MedicalCard = obj.key.CBM;
},
appointBtn(){  // 预约
this.$ajax.post('',{
"MSH.1": "doctorregistration",
"MSH.2": "addDoctorRegistration",
token:this.$root.loginInfo.token, //token
jzkbm:this.MedicalCard ,     // 就诊卡编码
ghysbm:this.doctorInfo.CRYBM,     // 挂号医生编码
hycm: this.timeList.CBH,     // 号源流水编号

}).then(res => {
if(typeof res.status != 'undefined' ){

if(res.status == '1'){
this.$vux.toast.show({
text: '预约成功!',
type: 'success'
});
this.$router.push({
name: 'appointmentDetails',
params: {
JLBM: res.ghbm,   // 挂号记录编码
INTENT: '1',   // 跳转界面标识 1==正常流程  2==我的预约
}
});

}else if(res.status == '2'){
this.$vux.toast.show({
text: '挂号失败，原因是本月取消次数已累计5次!',
type: 'cancel'
});

}else if(res.status == '3'){
this.$vux.toast.show({
text: '挂号失败，原因是同一就诊卡， 不能同一时间段预约3+个医生',
type: 'cancel'
});

}else if(res.status == '4'){
this.$vux.toast.show({
text: '同一就诊卡， 不能同一时间段预约同医院同科室的医生',
type: 'cancel'
});

}else{
this.$vux.toast.show({
text: '其他错误'+res.status,
type: 'cancel'
});
}

}

}).catch(error => {
console.log(error);
})
}
}
}



